.main{
  width: 100%;
  height: calc(100vh - 70px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.cards {
  width: 150px;
  height: 150px;
  margin: 5px;
  position: relative;
  cursor: pointer;
}

.card{
    width: 100%;
    height: 100%;
    position: absolute;
    transition: transform 1s;
    transform-style: preserve-3d;
}

.card div {
  display: block;
  height: 100%;
  width: 100%;
  position: absolute;
}

.card .back {
  transform: rotateY( 180deg );
  background: linear-gradient(#20E4A4, #2CC9CC);
  transition: 1s;
}
.cards:hover .back{
  transition: 0.5s;
  /* border: 2px solid #00be85; */
  border: 1px solid rgba(0,0,0,.125);
}

.card.flipped {
  transform: rotateY( 180deg );
}

header{
  width: 100%;
  height: 70px;
  background: linear-gradient(to left,#20E4A4, #2CC9CC);
  display: flex !important;
  align-items: center;
  justify-content: center;
}

.form-label{
  text-align: center !important;
}

.dropdown-menu{
  min-width: 120px !important;
}
.fade{
  margin: 0 200px 0 0 !important;
}

.dropdown-toggle, .fade{
  height: 50px !important;
}

.win-block{
  width: 500px;
  height: 500px;
  border: #8BF5F5 5px solid;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.win-block img{
  width: 490px;
}

.score{
  font-size: 20px;
  font-weight: bold;
  color: #28a745;
}

@media (max-width: 1200px){
  .cards {
    width: 100px;
    height: 100px;
  }
}

@media (max-width: 1000px){
  .cards {
    width: 80px;
    height: 80px;
  }
}

@media (max-width: 770px){
  .cards {
    width: 50px;
    height: 50px;
  }
}


@media (max-width: 430px){
  .fade {
    margin: 0 !important;
  }
  .cards {
    width: 35px;
    height: 35px;
  }
}